.Poster {
  margin-right: 4px;
  display: inline-block;
  transition: transform 300ms;
}

.Poster:hover {
  transform: scale(1.2);
  border-radius: 2%;
}

.Poster img {
  max-height: 169px;
  object-fit: contain;
  border-radius: 2%;
}

.Poster .imgAsPoster {
  max-height: 450px;
}
